


























































import { Component, Vue, Ref } from "vue-property-decorator";
import CallToAction from "../CallToAction.vue";
import FixedHeader from "../../components/header/FixedHeader.vue";
import Api from "../../Api";

@Component({
  components: {
    CallToAction,
    FixedHeader,
  },
})
export default class OurPrograms extends Vue {
  programsBrief = ";";
  programs = [
    {
      id: 0,
      Name: "",
      Image: "",
      Short_Brief: "",
    },
  ];
  // programs=[{}];
  programNameParam(name: string) {
    return name.split(" ").join("-");
  }
  async created() {
    await this.fetchPrograms();
  }

  async fetchPrograms() {
    await fetch(Api.Programs.GetAll)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.programs = data.data;
        for (const id in this.programs) {
          this.programs[id].Image =
            Api.LocalhostImages + this.programs[id].Image;
        }
      })
      .catch((error) => {
        console.log(error);
      });

    await fetch(Api.Programs.GetBrief)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.programsBrief = data.data.Brief;
      })
      .catch((error) => {
        console.log(error);
      });
  }
}
